//main: style.less

@media(max-width: 768px){
    .intro {
        max-width: 725px;
        margin-top: 90px;
    }

    .post {
        &__vidget {
            max-width: 680px;
            margin: 0 auto;
        }

        &__items {
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            height: 610px;
        }
    }

    .work {

        height: none;

        &__item {
            display: block;

            max-width: 500px;
            width: 100%;
            margin: 0 auto;
            margin-top: 50px;

            text-align: center;
        }

        &__widget {
            margin: 0 auto;
            margin-top: 25px;
        }
    }

    .footer {
        margin-top: 0;
    }

    .blog {

        h2 {
            max-width: 90px;
            margin: 0 auto;
        }

        &__content {
            align-items: center;
        }

    }
}

@media (max-width: 375px) {
    
    .blog {
        margin-top: 80px;
    }

    .container {
        max-width: 360px;
        padding: 0px 0px;
    }

    .btn {
        margin: 0 auto;
    }
    
    .header {
        margin-top: 0;
        position: relative;

        .burger {
            display: block;
        }

        &__nav {
            display: none;
        }

        &__btn {
            max-width: 30px;
            width: 100%;
            height: 20px;
            position: absolute;
            transition: .2s;
            right: 0;
            margin-right: 15px;
            margin-top: 15px;
            display: block;

            span,
            &::after,
            &::before {
                content: "";
                max-width: 30px;
                width: 100%;
                height: 2px;
                background-color: @black;
                display: block;
                position: absolute;
            }

            span {
                margin-top: 9px;
            }

            &::after {
                top: 0;
            }

            &::before {
                bottom: 0;
            }
        }
    }

    .intro {
        margin-top: 50px;
        text-align: center;

        &__content {
            flex-wrap: wrap-reverse;
            justify-content: center;
        }

        &__text {
            margin-top: 35px;
            text-align: center;
        }

        &__btn {
            margin-top: 25px;
        }
    }

    .post {

        &__vidget {
            justify-content: center;

            a {
                display: none;
            }
        }

        &__items {
            justify-content: space-between;
            height: 670px;
        }

        &__item {
            max-width: 305px;
            height: 270px;
        }
    }

    .work {
        text-align: center;

        &__item {
            max-width: 345px;

            img {
                height: 230px;
                max-width: 345px;
                border-radius: 10px;
            }
        }

        &__text {
            text-align: left;

            h2 {
                margin-top: 15px;
            }
        }

        &__widget {
            margin: 0;
            margin-top: 15px;
        }

        &-body {
            margin: 0 auto;
            margin-top: 80px;
        }
    }

    .burger {
        height: 100%;
        width: 100%;
        position: fixed;
        background-color: #fff;
        top: -100%;
        z-index: 2;
        transition: .5s;

        &__header {
            display: flex;
            align-items: center;
            position: relative;

            width: 100%;
            height: 50px;
            background-color: @black;

            h2 {
                color: #fff;
                margin: 0 auto;
                font-size: 30px;
            } 
        }

        &__logo {
            max-width: 30px;
            width: 100%;
            height: 20px;
            position: relative;
            transition: .2s;
            position: absolute;
            right: 0;
            margin-right: 15px;
            z-index: 2;
            

            span,
            &::after,
            &::before {
                content: "";
                width: 100%;
                height: 2px;
                background-color: #fff;
                display: block;
                position: absolute;
            }

            span {
                transform: rotate(45deg);
                margin-top: 9px;
            }

            &:after {
                transform: rotate(45deg);
                top: 9px;
            }

            &:before {
                transform: rotate(-45deg);
                bottom: 9px;
            }
        }

        &.active {
            top: 0;
        }

        &__content {
            display: flex;
            align-items: center;
            justify-content: center;
        }

        &__nav {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            max-width: 150px;
            width: 100%;
            margin-top: 50px;

            a {
                color: @black;
                font-size: 30px;
                transition: .2s;

                &:hover {
                    color: #FF6464;
                }
            }

            hr {
                width: 100%;
                height: 2px;
                background-color: #e0e0e0;

                border: none;
            }
        }
    }

}

@media (max-width: 320px) {
    
    .container {
        padding: 0;
    }

    .intro__text {
        p {
            max-width: 300px;
            margin: 0 auto;
            margin-top: 30px;
        }
    }

    .post {

        &__items {
            height: 720px;
        }

        &__item {
            max-width: 250px;
            height: 295px;

            h2 {
                font-size: 25px;
            }
        }

        &__sub-title {
            margin-top: 10px;

            p {
                font-size: 16px;
            }
        }
    }

    .work {

        &__item {
            max-width: 300px;
        }
    }

    .blog {

        &__item {
            max-width: 300px;
        }

        h2 {
            margin: 0 auto;
        }
    }

    .blog__content {
        max-width: 300px;
        margin: 0 auto;
        margin-top: 45px;
    }

}