//main: style.less

.footer {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 180px;
    margin-top: 80px;

    &__content {
        max-width: 255px;
        width: 100%;
    }

    &__message {
        display: flex;
        justify-content: space-between;
        width: 100%;

        img {
            max-width: 30px;
            width: 100%;
            height: 30px;
        }

        a {
            transition: .2s;
        }

        a:hover {
            transform: translate(0px, -5px);
        }
    }

    p {
        margin-top: 25px;
    }
}