//main: style.less

.post {
    width: 100%;
    margin-top: 70px;
    padding: 25px 0px;

    background-color: #EDF7FA;

    &__content {
        width: 100%;
    }

    &__vidget {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;

        h3 {
            font-size: 22px;
            color: @black;
        }

        a {
            font-size: 16px;
            color: #00A8CC;

        }
    }

    &__items {
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin-top: 25px;
    }

    &__item {
        max-width: 370px;
        width: 100%;
        height: 245px;
        padding: 25px;

        background-color: #fff;

        h2 {
            line-height: 2.3rem;
        }

        & > p {
            margin-top: 25px;
        }
    }

    &__sub-title {
        display: flex;
        justify-content: space-between;

        max-width: 280px;
        width: 100%;
        margin-top: 30px;

        p {
            font-size: 18px;
        }

        div {
            height: 20px;
            width: 2px;
            background-color: @black;
        }

    }
}
