//main:style.less

.header {
    display: flex;
    width: 100%;
    margin-top: 30px;
    position: relative;

    &__nav {
        display: flex;
        justify-content: space-between;

        max-width: 300px;
        width: 100%;

        position: absolute;
        right: 0;
        margin-right: 60px;

        a {
            transition: .2s;
            color: @black;
        }

        a:hover {
            color: #FF6464;
        }

    }

    &__btn {
        display: none;
    }
    .burger {
        display: none;
    }
}
