// compress: true

@import "vars.less";

body,html {
    margin: 0;
    padding: 0;
}

* {
    font-family: @ff;
}

p {
    color: #21243D;
    line-height: 1.6rem;
    font-size: 16px;
    margin: 0;
}

h3 {
    font-weight: normal;
    font-size: 22px;
    color: @black;
}

h2 {
    font-size: 26px;
    line-height: 1.7rem;
    color: @black;
    font-weight: 700;
}

h1,h2,h3 {
    margin: 0;
}

a {
    text-decoration: none;
    font-size: 20px;
    font-weight: 500;
}

.container {
    max-width: 855px;
    width: 100%;
    margin: 0 auto;
    padding: 0px 5px;
}

.btn {
    display: inline-block;
    padding: 10px 20px;

    border: 3px solid #FF6464;

    color: #FF6464;
    font-weight: 500;
    font-size: 20px;

    cursor: pointer;

    transition: .2s
}

.btn:hover {
    background-color: #FF6464;
    color: #fff;
}

