//main: style.less

.work {
    width: 100%;
    margin-top: 40px;

    &__items {
        display: flex;
        justify-content: space-between;
        flex-direction: column;

        width: 100%;
        margin-top: 40px;
    }

    &__item {
        display: flex;
        justify-content: space-between;

        width: 100%;
        margin-top: 45px;

        img {
            max-width: 245px;
            width: 100%;
            height: 180px;
            object-fit: cover;
        }

        &:first-child {
            margin-top: 0;
        }
    }

    &__text {
        max-width: 570px;
        width: 100%;

        & > p {
            margin-top: 25px;
        }

        h2 {
            margin-top: 5px;
        }
    }

    &__widget {
        display: flex;
        justify-content: space-between;

        max-width: 195px;
        width: 100%;
        margin-top: 25px;

        p {
            font-size: 20px;
            color: #8695A4;
        }
    }

    p.work__date {
        display: inline-block;

        padding: 0px 10px;
        background-color: #142850;
        border-radius: 30px;

        font-size: 18px;
        color: #fff;
    }

    hr {
        height: 1px;
        background-color: #E0E0E0;
        margin-top: 30px;

        border: none;
    }
}