//main: style.less

.intro {
    max-width: 855px;
    width: 100%;
    margin: 0 auto;
    margin-top: 140px;
}

.intro__content {
    display: flex;
    justify-content: space-between;

    width: 100%;

    img {
        max-width: 245px;
        width: 100%;
        height: 245px;

        object-fit: cover;
    }

}

.intro__text {
    max-width: 445px;
    width: 100%;

    h1 {
        font-size: 44px;
        font-weight: 600;
        color: @black;
    }

    p {
        margin-top: 40px;
    }
}

.intro__btn {
    margin-top: 40px;
}