//main: style.less

.blog {
    margin-top: 160px;

    h2 {
        font-size: 44px;
    }

    &__content {
        display: flex;
        flex-direction: column;

        margin-top: 50px;
    }

    &__item {
        max-width: 610px;
        width: 100%;
        margin-top: 30px;

        h3 {
            font-size: 30px;
            font-weight: 500;
            color: @black;
        }

        & > p {
            margin-top: 15px;
        }

        & > .post__sub-title {
            margin-top: 10px;
        }

        hr {
            height: 2px;
            background-color: #E0E0E0;
            border: none;
            margin-top: 30px;
        }

        &:first-child {
            margin-top: 0;
        }
    }
}